<template>
  <nav class="nav-container">
    <div class="nav-wrapper">
      <router-link to="/" class="nav-link">
        <span class="nav-icon">üè†</span>
        <span class="nav-text">Êµé‰∫ãÊ•º4Â±ÇÂØºËßà</span>
      </router-link>
      <div class="search-container" style="position: relative;">
        <input
          v-model="searchQuery"
          type="text"
          placeholder="ÊêúÁ¥¢..."
          class="search-input"
          @input="handleSearch"
        />
        <ul v-if="showDropdown" class="search-dropdown">
          <li
            v-for="room in filteredRooms"
            :key="room.id + room.name"
            @click="selectRoom(room)"
            class="dropdown-item"
          >
            {{ room.name }} - {{ room.function }}
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import { rooms } from '../rooms';

export default {
  data() {
    return {
      searchQuery: ""
    };
  },
  computed: {
    filteredRooms() {
      const q = this.searchQuery.trim().toLowerCase();
      if (!q) return [];
      // ËÆ°ÁÆóÂåπÈÖçÂàÜÊï∞ÔºåÊåâÂàÜÊï∞ÂÄíÂ∫èÊéíÂ∫è
      const scored = rooms.map(room => {
        let score = 0;
        // Ê£ÄÊü• id, name, function, responsible
        ['id', 'name', 'function', 'responsible'].forEach(key => {
          if (room[key] && room[key].toLowerCase().includes(q)) {
            score += 10;
          }
        });
        // Ê£ÄÊü• teachers Êï∞ÁªÑ
        if (room.teachers) {
          room.teachers.forEach(teacher => {
            if (teacher.toLowerCase().includes(q)) {
              score += 5;
            }
          });
        }
        return { room, score };
      }).filter(item => item.score > 0);
      // ÊåâÂàÜÊï∞ÈôçÂ∫èÊéíÂ∫è
      scored.sort((a, b) => b.score - a.score);
      return scored.map(item => item.room);
    },
    showDropdown() {
      return this.searchQuery.trim() !== "" && this.filteredRooms.length > 0;
    }
  },
  methods: {
    handleSearch() {
      this.$emit("search", this.searchQuery);
    },
    selectRoom(room) {
      this.$emit("select-room", room);
      this.searchQuery = "";
    },
    getRoomInfo(roomId) {
      return rooms.find(room => room.id === roomId);
    }
  },
}

</script>

